@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
.BarChart-chart {
  width: 100%; 
  min-height: 150px;
  padding: 10px;
  box-sizing: border-box;
  border-radius: 10px;
  position: relative;

}

.BarChart-bar {
  background: #5E7B7C;
  padding: 4px;
  box-sizing: border-box;
  border: 1px solid #02779E;
  border-radius: 2px;
  overflow-x: scroll;
}

.Key-title {
  text-transform: uppercase;
}

.Progression-bar {
  width: 100%;
  height: 20px;
  display: flex; 
  box-shadow:  20px 50px rgba(0,0,0,0.1);
  background: linear-gradient(-90deg, #70dc23, yellow, #ff1076);
}
.search-box {
	display: inline-block;
}

.search-input-date {
	padding: 5px;
	text-align: center;
}
.search-box {
	display: inline-block;
	text-align: left;
}

.search-input-ticker {
	padding: 5px;
	text-align: center;

}
.Stocks-Details {
  margin-top: 15px;
  padding: 10px;
  display: grid;
  background: white;
  box-shadow: 3px 3px 1px 1px rgba(0, 0, 0, 0.3);
  grid-template-columns: 200px 1fr;
}

.Stock-Name {
  font-family: 'Roboto', sans-serif;
  text-transform: uppercase;
  font-size: 14pt;
  display: inline-block;
  padding-left: 20px;
}

.Stock-Currency {
  font-family: 'Roboto', sans-serif;
  text-transform: uppercase;
  font-size: 14pt;
  display: inline-block;
  padding-left: 20px;
}

.Stock-Prices {
  font-size: 20pt;
  font-family: 'Roboto', sans-serif;
  padding-right: 10px;
}
.stock-info-section {
  width: 50%; 
  min-height: 150px;
  padding: 1px;
  box-sizing: border-box;
  border-radius: 2px;
  position: relative;
}

.stock-info-area {
  font-size: 20px;
  font-family: 'Roboto', sans-serif;
}

.stock-info-details {
  font-weight: bold;
}

.stock-info-name {
  text-align: center;
  font-size: 70px;
}
